import * as Actions from "actions";

import {IAppState, IPaletteState, ICanvasState} from "state";

const initialState = {
  palette: {selectedShapeType: undefined},
  canvas: {shapes: [], selectedShapeIndex: null},
};

function paletteReducer(paletteState: IPaletteState, action: Actions.TAction): IPaletteState {
  switch (action.type) {
    case Actions.SELECT_SHAPE:
      const paletteAction = action as Actions.IPaletteAction;
      return {
        selectedShapeType: paletteAction.shapeType,
      };
  }
  return paletteState;
}

function canvasReducer(canvasState: ICanvasState, action: Actions.TAction): ICanvasState {
  switch (action.type) {
    case Actions.DROP_SHAPE:
      const dropAction: Actions.IShapeDropAction = action as Actions.IShapeDropAction;
      return {
        selectedShapeIndex: canvasState.shapes.length,
        shapes: canvasState.shapes.concat([
          {
            shapeType: dropAction.shapeType,
            x: dropAction.x,
            y: dropAction.y,
            id: canvasState.shapes.length,
          },
        ]),
      };
    case Actions.SELECT_SHAPE_ON_CANVAS:
      const selectShapeOnCanvasAction = action as Actions.ISelectShapeOnCanvasAction;
      return {
        ...canvasState,
        selectedShapeIndex: selectShapeOnCanvasAction.index,
      };
    case Actions.MOVE_SHAPE_ON_CANVAS:
      const moveShapeOnCanvasAction = action as Actions.IMoveShapeOnCanvasAction;
      return {
        ...canvasState,
        shapes: canvasState.shapes.map((s, i) => {
          if (i === canvasState.selectedShapeIndex) {
            return {
              ...s,
              x: s.x + moveShapeOnCanvasAction.dx,
              y: s.y + moveShapeOnCanvasAction.dy,
            };
          }
          return s;
        }),
      };
    case Actions.BRING_SHAPE_TO_FRONT:
      if (canvasState.selectedShapeIndex === null || canvasState.selectedShapeIndex >= canvasState.shapes.length - 1) {
        return canvasState;
      }
      return {
        selectedShapeIndex: canvasState.selectedShapeIndex + 1,
        shapes: swapTwoAdjacentItems(canvasState.shapes, canvasState.selectedShapeIndex),
      };
    case Actions.PUSH_SHAPE_BACK:
      if (canvasState.selectedShapeIndex === null || canvasState.selectedShapeIndex === 0) {
        return canvasState;
      }
      return {
        selectedShapeIndex: canvasState.selectedShapeIndex - 1,
        shapes: swapTwoAdjacentItems(canvasState.shapes, canvasState.selectedShapeIndex - 1),
      };
  }
  return canvasState;
}

function swapTwoAdjacentItems<T>(array: T[], firstIndex: number): T[] {
  return [...array.slice(0, firstIndex), array[firstIndex + 1], array[firstIndex], ...array.slice(firstIndex + 2)];
}

export function reducer(state: IAppState = initialState, action: Actions.TAction): IAppState {
  return {
    palette: paletteReducer(state.palette, action),
    canvas: canvasReducer(state.canvas, action),
  };
}
