import * as React from "react";
import * as Styles from "components/palette/ShapesPalette.css";
import {connect} from "react-redux";
import PaletteItem from "components/palette/PaletteItem";
import {shapeFactory} from "components/shapes/Shapes";
import {dropShapeToCanvas, selectShapeOnPalette, bringShapeToFront, pushShapeBack} from "actions";
import {IAppState, ShapeType} from "state";

interface IProps {
  selectedShapeType?: ShapeType;
  isShapeOnCanvasSelected: boolean;
  selectedShapeLayer: number | null;
  isFirstLayerSelected: boolean;
  isLastLayerSelected: boolean;
}

interface IReduxActions {
  selectShapeOnPalette: typeof selectShapeOnPalette;
  dropShapeToCanvas: typeof dropShapeToCanvas;
  bringShapeToFront: typeof bringShapeToFront;
  pushShapeBack: typeof pushShapeBack;
}

function mapStateToProps(state: IAppState): IProps {
  return {
    selectedShapeType: state.palette.selectedShapeType,
    isShapeOnCanvasSelected: state.canvas.selectedShapeIndex !== null,
    selectedShapeLayer: state.canvas.selectedShapeIndex,
    isFirstLayerSelected: state.canvas.selectedShapeIndex === 0,
    isLastLayerSelected: state.canvas.selectedShapeIndex === state.canvas.shapes.length - 1,
  };
}

class ShapesPalette extends React.Component<IProps & IReduxActions> {
  private readonly PaletteItemPadding = 20;
  public render(): JSX.Element {
    const {isShapeOnCanvasSelected} = this.props;
    return (
      <div className={Styles.palette}>
        {this.renderPaletteItem(ShapeType.Circle)}
        {this.renderPaletteItem(ShapeType.Square)}
        {this.renderPaletteItem(ShapeType.Triangle)}
        {isShapeOnCanvasSelected && this.renderLayerButtons()}
      </div>
    );
  }

  private renderPaletteItem(shapeType: ShapeType): JSX.Element {
    const {selectedShapeType} = this.props;
    return (
      <PaletteItem shapeType={shapeType} onClick={this.clickHandler} onDrop={this.dropHandler}>
        {shapeFactory(
          shapeType,
          {
            x: this.PaletteItemPadding,
            y: this.PaletteItemPadding,
            isSelected: selectedShapeType === shapeType,
          },
          shapeType
        )}
      </PaletteItem>
    );
  }

  private renderLayerButtons(): JSX.Element {
    const {selectedShapeLayer, isFirstLayerSelected, isLastLayerSelected} = this.props;
    return (
      <div className={Styles.layerButtonContainer}>
        Layer: {selectedShapeLayer}
        <br />
        <button onClick={this.bringFrontClickHandler} className={Styles.layerButton} disabled={isLastLayerSelected}>
          Bring To Front
        </button>
        <button onClick={this.pushShapeBack} className={Styles.layerButton} disabled={isFirstLayerSelected}>
          Push Back
        </button>
      </div>
    );
  }

  private clickHandler = (shapeType: ShapeType) => {
    this.props.selectShapeOnPalette(shapeType);
  };

  private dropHandler = (shapeType: ShapeType, x: number, y: number) => {
    this.props.dropShapeToCanvas(shapeType, x + this.PaletteItemPadding, y + this.PaletteItemPadding);
  };

  private bringFrontClickHandler = () => {
    this.props.bringShapeToFront();
  };

  private pushShapeBack = () => {
    this.props.pushShapeBack();
  };
}

export default connect(
  mapStateToProps,
  {selectShapeOnPalette, dropShapeToCanvas, bringShapeToFront, pushShapeBack}
)(ShapesPalette);
